"use client"

import { useState } from "react"
import { useQuery, useMutation, useQueryClient } from "@tanstack/react-query"
import { ArrowLeft, Calendar, Check, X, AlertCircle } from "lucide-react"
import { useRouter } from "next/navigation"
import { Card, Button, Badge, SectionLoader } from "@/components/ui"
import { MAX_PAUSE_DAYS_PER_MONTH } from "@/lib/constants"
import { formatDateShort, getMonthDates, isSameDay, getISTDate } from "@/lib/utils"
import toast from "react-hot-toast"

export default function PausePage() {
  const router = useRouter()
  const queryClient = useQueryClient()
  const [selectedDates, setSelectedDates] = useState<Date[]>([])

  const { data, isLoading } = useQuery({
    queryKey: ["pause-status"],
    queryFn: async () => {
      const res = await fetch("/api/customer/pause")
      if (!res.ok) throw new Error("Failed to fetch")
      return res.json()
    },
  })

  const pauseMutation = useMutation({
    mutationFn: async (dates: string[]) => {
      const res = await fetch("/api/customer/pause", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({ dates }),
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Delivery paused successfully")
      queryClient.invalidateQueries({ queryKey: ["pause-status"] })
      setSelectedDates([])
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  const cancelMutation = useMutation({
    mutationFn: async (date: string) => {
      const res = await fetch(`/api/customer/pause?date=${date}`, {
        method: "DELETE",
      })
      if (!res.ok) {
        const data = await res.json()
        throw new Error(data.error)
      }
      return res.json()
    },
    onSuccess: () => {
      toast.success("Pause cancelled")
      queryClient.invalidateQueries({ queryKey: ["pause-status"] })
    },
    onError: (err: any) => {
      toast.error(err.message)
    },
  })

  if (isLoading) return <SectionLoader />

  const pauseData = data?.data
  const remainingDays = pauseData?.remainingPauseDays || 0
  const upcomingPauses = pauseData?.upcomingPauses || []
  const earliestPausable = new Date(pauseData?.earliestPausableDate)

  // Generate calendar for current and next month
  const today = getISTDate()
  const currentMonth = getMonthDates(today.getFullYear(), today.getMonth() + 1)
  const nextMonth = getMonthDates(today.getFullYear(), today.getMonth() + 2)

  const toggleDate = (date: Date) => {
    if (date < earliestPausable) return
    
    const isSelected = selectedDates.some(d => isSameDay(d, date))
    const isPaused = upcomingPauses.some((p: any) => isSameDay(new Date(p.pauseDate), date))
    
    if (isPaused) return
    
    if (isSelected) {
      setSelectedDates(selectedDates.filter(d => !isSameDay(d, date)))
    } else {
      if (selectedDates.length + pauseData.pauseDaysUsed >= MAX_PAUSE_DAYS_PER_MONTH) {
        toast.error(`You can only pause ${MAX_PAUSE_DAYS_PER_MONTH} days per month`)
        return
      }
      setSelectedDates([...selectedDates, date])
    }
  }

  const handlePause = () => {
    const dateStrings = selectedDates.map(d => d.toISOString().split('T')[0])
    pauseMutation.mutate(dateStrings)
  }

  const renderCalendar = (dates: Date[], monthLabel: string) => {
    const firstDay = dates[0].getDay()
    const emptyDays = Array(firstDay).fill(null)

    return (
      <div className="mb-6">
        <h3 className="font-semibold text-neutral-900 mb-3">{monthLabel}</h3>
        <div className="grid grid-cols-7 gap-1 text-center text-xs text-neutral-500 mb-2">
          {['S', 'M', 'T', 'W', 'T', 'F', 'S'].map((d, i) => (
            <div key={i} className="py-1">{d}</div>
          ))}
        </div>
        <div className="grid grid-cols-7 gap-1">
          {emptyDays.map((_, i) => (
            <div key={`empty-${i}`} />
          ))}
          {dates.map((date) => {
            const isPast = date < earliestPausable
            const isPaused = upcomingPauses.some((p: any) => 
              isSameDay(new Date(p.pauseDate), date)
            )
            const isSelected = selectedDates.some(d => isSameDay(d, date))
            const isToday = isSameDay(date, today)

            return (
              <button
                key={date.toISOString()}
                onClick={() => toggleDate(date)}
                disabled={isPast || isPaused}
                className={`
                  aspect-square rounded-lg text-sm font-medium transition-all
                  ${isPast ? "text-neutral-300 cursor-not-allowed" : ""}
                  ${isPaused ? "bg-amber-100 text-amber-700" : ""}
                  ${isSelected ? "bg-primary-600 text-white" : ""}
                  ${!isPast && !isPaused && !isSelected ? "hover:bg-neutral-100" : ""}
                  ${isToday && !isPaused && !isSelected ? "ring-2 ring-primary-300" : ""}
                `}
              >
                {date.getDate()}
              </button>
            )
          })}
        </div>
      </div>
    )
  }

  return (
    <div className="p-4">
      {/* Header */}
      <div className="flex items-center gap-4 mb-6">
        <button
          onClick={() => router.back()}
          className="h-10 w-10 flex items-center justify-center rounded-xl bg-neutral-100"
        >
          <ArrowLeft className="h-5 w-5" />
        </button>
        <h1 className="text-xl font-bold text-neutral-900">Pause Delivery</h1>
      </div>

      {/* Remaining Days */}
      <Card className="mb-4">
        <div className="flex items-center justify-between">
          <div>
            <p className="text-sm text-neutral-500">Pause days remaining</p>
            <p className="text-2xl font-bold text-neutral-900">
              {remainingDays} <span className="text-base font-normal text-neutral-500">/ {MAX_PAUSE_DAYS_PER_MONTH}</span>
            </p>
          </div>
          <Calendar className="h-8 w-8 text-primary-600" />
        </div>
      </Card>

      {/* Info */}
      <Card className="mb-4 bg-blue-50 border-blue-200">
        <div className="flex gap-3">
          <AlertCircle className="h-5 w-5 text-blue-600 shrink-0 mt-0.5" />
          <div className="text-sm text-blue-700">
            <p className="font-medium mb-1">Important</p>
            <ul className="list-disc list-inside space-y-1">
              <li>Changes must be made before 5 PM the previous day</li>
              <li>You can pause up to {MAX_PAUSE_DAYS_PER_MONTH} days per month</li>
              <li>Pause days don't carry forward to next month</li>
            </ul>
          </div>
        </div>
      </Card>

      {/* Upcoming Pauses */}
      {upcomingPauses.length > 0 && (
        <Card className="mb-4">
          <h3 className="font-semibold text-neutral-900 mb-3">Upcoming Pauses</h3>
          <div className="space-y-2">
            {upcomingPauses.map((pause: any) => {
              const pauseDate = new Date(pause.pauseDate)
              const canCancel = pauseDate >= earliestPausable

              return (
                <div key={pause.id} className="flex items-center justify-between p-3 bg-amber-50 rounded-xl">
                  <div className="flex items-center gap-3">
                    <Calendar className="h-5 w-5 text-amber-600" />
                    <span className="font-medium text-amber-800">
                      {formatDateShort(pause.pauseDate)}
                    </span>
                  </div>
                  {canCancel && pause.createdByCustomer && (
                    <button
                      onClick={() => cancelMutation.mutate(pauseDate.toISOString().split('T')[0])}
                      className="h-8 w-8 flex items-center justify-center rounded-lg bg-amber-100 text-amber-700 hover:bg-amber-200"
                    >
                      <X className="h-4 w-4" />
                    </button>
                  )}
                </div>
              )
            })}
          </div>
        </Card>
      )}

      {/* Calendar */}
      <Card className="mb-4">
        <p className="text-sm text-neutral-500 mb-4">
          Tap on dates to select. Yellow dates are already paused.
        </p>
        {renderCalendar(currentMonth, today.toLocaleString('default', { month: 'long', year: 'numeric' }))}
        {renderCalendar(nextMonth, new Date(today.getFullYear(), today.getMonth() + 1).toLocaleString('default', { month: 'long', year: 'numeric' }))}
      </Card>

      {/* Confirm Button */}
      {selectedDates.length > 0 && (
        <div className="fixed bottom-20 left-0 right-0 p-4 bg-gradient-to-t from-background via-background to-transparent">
          <Button
            onClick={handlePause}
            loading={pauseMutation.isPending}
            fullWidth
            size="lg"
          >
            <Check className="h-5 w-5" />
            Pause {selectedDates.length} Day{selectedDates.length > 1 ? 's' : ''}
          </Button>
        </div>
      )}
    </div>
  )
}
